"use client";
import { useState } from "react";
import educationData from "./data"; // Chemin vers le fichier data.js

export default function Contact() {
  const [level, setLevel] = useState("");
  const [profile, setProfile] = useState("");
  const [topic, setTopic] = useState("");

  const handleLevelChange = (e) => {
    const selectedLevel = e.target.value;
    setLevel(selectedLevel);
    setProfile("");
    setTopic("");
  };

  const handleProfileChange = (e) => {
    const selectedProfile = e.target.value;
    setProfile(selectedProfile);
    setTopic("");
  };

  const handleTopicChange = (e) => {
    setTopic(e.target.value);
  };

  const profiles =
    level && educationData[level]
      ? Object.keys(educationData[level].profiles)
      : [];

  const topics =
    level && profile && educationData[level]?.profiles[profile]
      ? educationData[level].profiles[profile]
      : [];

  return (
    <div className="max-w-screen-lg mx-auto pt-10 p-5 py-12">
      <h2 className="text-3xl font-bold text-slate-900 mb-8 text-center">
        MAKE YOUR DREAM TRUE
      </h2>
      <div className="grid grid-cols-1 md:grid-cols-12 border">
        <div className="bg-gray-900 md:col-span-4 p-10 text-white">
          <h3 className="text-3xl sm:text-4xl leading-normal font-extrabold tracking-tight">
            APPLY <span className="text-indigo-600">NOW</span>
          </h3>
          <p className="mt-4 leading-7 text-gray-200">
            We are there to help you get and make your dream true.!
          </p>
          <div className="flex items-center mt-5 text-sm">
            <span>Moscou, Russian.</span>
          </div>
          <div className="flex items-center mt-5 text-sm">
            <span>+93 749 99 65 50</span>
          </div>
          <div className="flex items-center mt-5 text-sm">
            <span>24/7</span>
          </div>
        </div>

        <form className="md:col-span-8 p-10">
          {/* ... NOM, TÉLÉPHONE, EMAIL ... */}

          {/* Niveau d'éducation */}
          <div className="mb-6">
            <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
              Level of Education
            </label>
            <select
              value={level}
              onChange={handleLevelChange}
              className="block w-full bg-gray-200 text-gray-700 border rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white"
            >
              <option value="">Select Level</option>
              {Object.keys(educationData).map((lvl) => (
                <option key={lvl} value={lvl}>
                  {lvl.charAt(0).toUpperCase() + lvl.slice(1)}
                </option>
              ))}
            </select>
          </div>

          {/* Profile */}
          <div className="mb-6">
            <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
              Profile
            </label>
            <select
              value={profile}
              onChange={handleProfileChange}
              className="block w-full bg-gray-200 text-gray-700 border rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white"
              disabled={!level}
            >
              <option value="">Select Profile</option>
              {profiles.map((p) => (
                <option key={p} value={p}>
                  {p.charAt(0).toUpperCase() + p.slice(1)}
                </option>
              ))}
            </select>
          </div>

          {/* Topic */}
          <div className="mb-6">
            <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
              Topic
            </label>
            <select
              value={topic}
              onChange={handleTopicChange}
              className="block w-full bg-gray-200 text-gray-700 border rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white"
              disabled={!profile}
            >
              <option value="">Select Topic</option>
              {topics.map((t) => (
                <option key={t} value={t}>
                  {t}
                </option>
              ))}
            </select>
          </div>

          {/* Message et bouton */}
          {/* ... RESTE DU FORMULAIRE ... */}
        </form>
      </div>
    </div>
  );
}
